import { ParsingCommentsDataBundlePropType } from "@interfaces/data-type/type.pages-comments";
import FlatList from "@module-flat-list/index.flat-list";
import { FlatItemList } from "@module-flat-list/index.item-list";
import { CallbackRender, Container, Dispatch, FooterMobileGlobal, HeaderGlobalPages, OwnIcon, StoragesSystem, } from "index-module";
import React, { Component } from "react";
import { connect } from "react-redux";
import jsonQuery from 'json-query';
import { RouteComponentProps, withRouter } from "react-router-dom";
import { ApiBody, BASE_URL, ServerRequest } from "@utils/utils-server-request";
import ProtectorContainer from "@module-layout/protector-container";
import { BoxLoader } from "@loader/box-loader";
import { IUsers } from "@module-flat-list/type";


export interface ScreenHomePropTypeSmall {
  StoragesSystem: any,
  setStorage?: (obj: ParsingCommentsDataBundlePropType) => void
}

export type ScreenHomePropType = ScreenHomePropTypeSmall & RouteComponentProps;



export interface ScreenHomeStateType {
  data: any,
  key: any,
  searchKey: any
  isReady?: any,
  isFound?: any, isError?: any, isFetching?: any,
  time?: any
  isEnd?: any
  page: number
}

const FormLoginLogicPropDefault: ScreenHomePropTypeSmall = {
  StoragesSystem: {
    catalog: [
    ]
  },
}


class ScreenHome extends Component<ScreenHomePropType, ScreenHomeStateType> {
  static defaultProps = FormLoginLogicPropDefault;
  constructor(props: any) {
    super(props)
    this.state = {
      data: [],
      key: 'title',
      searchKey: "",
      isFetching: false,
      page: 1
    }
  }

  componentDidMount(): void {
    this.getData(true)
  }

  onScroll = (e: any) => {
    if (e.isBottom && !this.state.isFetching && !this.state.isEnd) {
      this.getData()
    }
  }


  getData = (isRefresh?: boolean) => {
    const token = this.props.StoragesSystem?.users?.token || ""
    let paramsStateRefresh = {}
    if (isRefresh) {
      paramsStateRefresh = { isEnd: false, page: 1, data: [], }
    }

    if (!this.state.isFetching) {
      this.setState({ isFetching: true, isFound: false, isError: false, ...paramsStateRefresh }, () => {
        const params = ['role=user', `page=${this.state.page}`];
        if (this.state.searchKey) {
          params.push(`query=${this.state.searchKey}`)
        }
        const config: ApiBody = {
          endpoint: `api/accounts/${token}/get/with-records?${params.join("&")}`,
        }
        new ServerRequest(BASE_URL).get(config, ({ data, isFound, isError, isFetching }) => {
          if (isFound) {
            let dataRes = data;
            if (!isRefresh) {
              dataRes = [...this.state.data, ...dataRes]
            }
            this.setState({ isFound, isError, isFetching, data: dataRes, isEnd: data.length < 1 && !isRefresh, page: (this.state.page + 1) })
          } else if (isError) {
            this.setState({ isFound, isError, isFetching })
          }
        })
      })
    }
  }

  componentDidUpdate = (prevProps: ScreenHomePropType) => {
    if (this.props.StoragesSystem !== prevProps.StoragesSystem) {
      // this.setState({
      //   catalog: this.props.StoragesSystem?.catalog || [],
      //   key: 'any',
      //   searchKey: '',
      // })
    }
  }

  private timeOut: any = undefined;
  onChange = (e: any) => {
    clearTimeout(this.timeOut)
    this.setState({ searchKey: e.value }, () => {
      this.timeOut = setTimeout(this.onSearch, 500)
    })
  }
  onSearch = () => this.getData(true)

  onSaveDone = (index: number) => (user: IUsers) => {
    const data = this.state.data;
    data[index] = user;
    this.setState({ data, time: new Date().getTime() })
  }

  doRefresh = () => {
    this.getData(true)
  }

  render() {
    const { props, state } = this;
    return (
      <ProtectorContainer>
        <HeaderGlobalPages onChange={this.onChange} isHome={true} />
        <Container className={"login_page_wrappe"}>
          <section className={"content_value content_value_login"}>

            <FlatList data={state.data} onScroll={this.onScroll}>
              {() => (
                <>
                  {state.data.map?.((items: any, index: number) => (
                    <FlatItemList doRefresh={this.doRefresh} onSaveDone={this.onSaveDone(index)} {...props} item={items} index={index} key={`${index}-index-${items.id}`} />
                  ))}
                  <BoxLoader isShow={state.isFetching} />
                </>
              )}
            </FlatList>

            {state.data.length < 1 && !state.isFetching &&
              <div className="error-list-empty">
                <div className="error-list-empty-inside">
                  <OwnIcon.IconAlert />
                  <h1>Belum ada ibu hamil</h1>
                </div>
              </div>
            }
          </section>
        </Container>
        <FooterMobileGlobal {...props} activePage="home" />
      </ProtectorContainer>
    )
  }
}

const mapStateToProps = (state: any) => ({
  StoragesSystem: state.StoragesSystem,
})
const mapDispatchToProps = (dispatch: Dispatch) => ({
  setStorage: ({ data, type }: ParsingCommentsDataBundlePropType) => dispatch(new StoragesSystem().setStorage({ data, type })),
})

export default withRouter(connect(mapStateToProps, mapDispatchToProps)(ScreenHome));
