import React from 'react';
import { LazyFoxInputBox, LazyFoxModalWindow, OwnIcon } from 'index-module';
import { ButtonMain } from '@module-button/buttons/index.main.button';
import { uuidv4 } from '@utils/utils-globals';
import FlatList from './index.flat-list';
import "./_records_item.scss"
import { EditIcon, Icons, Trash, Whatsapp } from './icons';
import { IUsers, Records } from './type';
import { ApiBody, BASE_URL, ServerRequest } from '@utils/utils-server-request';
import { GlobalLoader } from '@loader/global-loader';
import { getBirtDay } from './utils';
import { WhatsappMessage } from './whatsapp-message';

const FlatListPropDefault: any = {

}
interface IRecordsItem {
  index: number
  record: Records
  user: IUsers
  StoragesSystem?: any
  setStorage?: any
  onSaveDone: (user: IUsers) => void
}
interface IRecordsItemState {
  dataRecords: Records | null
  isDelete?: boolean
  value: any,
  isFound?: any, isError?: any, isFetching?: any, message?: any
}

export class RecordsItem extends React.Component<IRecordsItem, IRecordsItemState> {
  constructor(props: IRecordsItem) {
    super(props);
    this.state = {
      dataRecords: null as Records | null,
      value: { dateJoin: null }
    }
  }
  private openModalWindow = React.createRef<LazyFoxModalWindow>()
  private openModalWindowAdd = React.createRef<LazyFoxModalWindow>()
  public static defaultProps = FlatListPropDefault;

  onOpenPupup = () => {
    if (this.openModalWindow.current) {
      this.openModalWindow.current?.show()
    }
  }

  onOpenPupupEdit = (record: Records) => (e: any) => {
    if (this.openModalWindowAdd.current) {
      this.setState({ dataRecords: record }, () => {
        this.openModalWindowAdd.current?.show()
      })
    }
  }

  onOpenDeletePopup = (record: Records) => (e: any) => {
    if (this.openModalWindow) {
      this.setState({ dataRecords: record }, () => {
        this.openModalWindow.current?.show()
      })
    }
  }

  onDismiss = (key: "remove" | "edit") => () => {
    this.setState({ dataRecords: null }, () => {
      if (key == "remove") {

        this.openModalWindow.current?.dismiss?.()
      }
      if (key == "edit") {

        this.openModalWindowAdd.current?.dismiss?.()
      }
    })
  }


  onSave = (e: any) => {
    const token = this.props.StoragesSystem?.users?.token || ""
    if (!this.state.value.dateJoin?.isError) {
      const config: ApiBody = {
        endpoint: `api/records/${token}/update/${this.state.dataRecords?.id}`,
        body: {
          dateJoin: this.state.value.dateJoin.value
        }
      }
      if (!this.state.isFetching) {
        this.setState({ isFetching: true })
        new ServerRequest(BASE_URL).post(config, ({ data, isFound, isError, isFetching, message }) => {
          if (isFound) {
            this.setState({ isFound, isError, isFetching, message }, () => {
              this.onDismiss('edit')()
              this.props.onSaveDone(data[0])
            })
          } else if (isError) {
            this.setState({ isFound, isError, isFetching, message })
          }
        })
      }
    }

  }

  onDeleteById = (e: any) => {
    const token = this.props.StoragesSystem?.users?.token || ""
    if (!this.state.value.dateJoin?.isError) {
      const config: ApiBody = {
        endpoint: `api/records/${token}/delete/${this.state.dataRecords?.id}`,
      }
      if (!this.state.isFetching) {
        this.setState({ isFetching: true })
        new ServerRequest(BASE_URL).delete(config, ({ data, isFound, isError, isFetching, message }) => {
          if (isFound) {
            this.setState({ isFound, isError, isFetching, message }, () => {
              this.onDismiss('remove')()
              this.props.onSaveDone(data[0])
            })
          } else if (isError) {
            this.setState({ isFound, isError, isFetching, message })
          }
        })
      }
    }
  }

  onChange = (e: any) => {
    this.setState({
      isError: false,
      value: {
        ...this.state.value,
        [e.idKey]: e
      }
    })
  }

  getBirtDay = (str: string): { day: string, date: string, full: string } => getBirtDay(str)

  render() {
    const { props, state } = this;

    return (
      <div className={"item-list-flat-list"} >
        <div className={"item-list-flat-list"} >
          <div className={"item-list-flat-list-inside"}>
            <div className={`medicine-title d_flex justify_content_between ${props.record.isExpired && "expired_box"}`} >
              <div className='d_flex'>
                <span className='numbering'>{(props.index + 1)}</span>
                <div className='d_flex flex_column'>
                  <span style={{ fontSize: '12px' }}>Tanggal Lahiran</span>
                  <span>{this.getBirtDay(props.record.dateJoin).full}</span>
                </div>
              </div>
              <span className='records_icon d_flex justify_content_between' >
                <EditIcon onClick={this.onOpenPupupEdit(props.record)} />
                <Trash onClick={this.onOpenDeletePopup(props.record)} />
              </span>
            </div>
            <div className={"medicine-content"}>
              <div className={'main-contents'}>
                <div className={"info"} >
                  {props.record.schedules.map((scheduleItem, i) => (
                    <div className={`schedule_item ${scheduleItem.remaining.class}`} key={i}>
                      <div className='d_flex'>
                        <span className={"stok"}>PK {i + 1}: <span className={""}>{this.getBirtDay(scheduleItem.date).full}</span></span>
                      </div>
                      <div className='d_flex schedule_action'>
                        <span className='active_status'>{scheduleItem.remaining.text}</span>
                        {!props.record.isExpired && (
                          <Whatsapp className={"send_icon"} onClick={WhatsappMessage(props.record.schedules, props.user, props.StoragesSystem?.users, i)} />
                        )}
                      </div>
                    </div>
                  ))}
                </div>
              </div>
            </div>
          </div>
        </div>

        <LazyFoxModalWindow onDismiss={this.onDismiss} className={"modal-window-center"} ref={this.openModalWindow} useHeader={true} headerTitle={"PERINGATAN !!!"}>
          <div className="success-info">
            <div className="text-success">
              <h2>Apakah anda yakin ingin menghapus {this.getBirtDay(props.record.dateJoin).full} ?</h2>
            </div>
            <div className='d_flex justify_content_between'>
              <ButtonMain className='dark-key' isDark={true} onClick={this.onDismiss("remove")} >
                <span>Batalkan</span>
              </ButtonMain>
              <ButtonMain isDark={true} onClick={this.onDeleteById}>
                <span>Hapus</span>
              </ButtonMain>
            </div>
            <GlobalLoader isShow={state.isFetching} />
          </div>
        </LazyFoxModalWindow>

        <LazyFoxModalWindow onDismiss={this.onDismiss} className={"modal-window-left"} ref={this.openModalWindowAdd} useHeader={true} headerTitle={"Waktu Persalinan"}>
          <div className="success-info ">
            <div className="text-success">
              {this.state.dataRecords?.dateJoin && (
                <h2>Anda akan mengubah Tanggal bersalin untuk</h2>
              )}
              {!this.state.dataRecords?.dateJoin && (
                <h2>Anda akan menambahkan Tanggal bersalin untuk</h2>
              )}
              <h2> ibu {props.user.name}</h2>
              <div>
                <LazyFoxInputBox idKey={"dateJoin"} onChange={this.onChange} value={this.getBirtDay(props.record.dateJoin).date} title='Tanggal Bersalin' type='date' />
              </div>
            </div>
            <ButtonMain isDark={true} onClick={this.onSave}>
              <span>Simpan</span>
            </ButtonMain>

            <GlobalLoader isShow={state.isFetching} />
          </div>
        </LazyFoxModalWindow>
      </div>
    )
  }
}
