import { ParsingCommentsDataBundlePropType } from "@interfaces/data-type/type.pages-comments";
import { Container, DataArray, Dispatch, HeaderFeedsDetailsPages, LazyFoxInputBox, LazyFoxModalWindow, LazyFoxModalWindowPropType, StoragesSystem, } from "index-module";
import React, { Component } from "react";
import { connect } from "react-redux";
import { withRouter } from "react-router-dom";
import { ScreenHomePropType, ScreenHomePropTypeSmall } from "./screen-home";
import { ButtonMain } from "@module-button/buttons/index.main.button";
import { uuidv4 } from "@utils/utils-globals";
import { ApiBody, BASE_URL, ServerRequest } from "@utils/utils-server-request";
import { GlobalLoader } from "@loader/global-loader";
import { Route, Redirect } from 'react-router-dom';
import ProtectorContainer from "@module-layout/protector-container";

export interface ScreenAddStateType {
  catalog: any,
  key: any,
  value: any,
  contentId?: any,
  indexId?: any
  isFound?: any, isError?: any, isFetching?: any,
  message?: any
}

const defaultProps: ScreenHomePropTypeSmall = {
  StoragesSystem: {
    catalog: []
  },
}

const GetInput = (key: "edit" | "save") => {
  if (key == "edit") {
    return [
      { title: "Nama *", type: "name", idKey: 'name' },
      { title: "Email", type: "email", idKey: 'email' },
      { title: "Whatsapp", type: "text", idKey: 'phone' },
      { title: "alamat", type: "null", idKey: 'address', isMultiline: true },
    ]
  }
  return [
    { title: "Nama *", type: "name", idKey: 'name' },
    { title: "Email", type: "email", idKey: 'email' },
    { title: "Whatsapp", type: "text", idKey: 'phone' },
    { title: "alamat", type: "null", idKey: 'address', isMultiline: true },
    { title: "Tanggal Melahirkan", type: "date", idKey: 'dateJoin', },
  ]
}

const generateValue = (obj: any = {}) => {
  let newObject = {};
  for (const key in obj) {
    newObject = {
      ...newObject,
      [key]: {
        isError: false,
        value: obj[key]
      }
    };
  }
  return newObject
}

const getValue = (obj: any = {}) => {
  let newObject = {};
  for (const key in obj) {
    newObject = {
      ...newObject,
      [key]: obj[key].value
    };
  }
  return newObject
}

class ScreenAdd extends Component<ScreenHomePropType, ScreenAddStateType> {
  static defaultProps = defaultProps;
  constructor(props: any) {
    super(props)
    this.state = {
      catalog: props.StoragesSystem?.catalog || [],
      key: 'title',
      isError: false,
      contentId: props.match?.params?.contentId,
      indexId: props.match?.params?.indexId,
      value: {
        email: {
          isError: false, value: `${new Date().getTime()}_mail@loombu.com`
        }
      }
    }
  }
  private openModalWindow = React.createRef<LazyFoxModalWindow>()

  onChange = (e: any) => {
    this.setState({
      isError: false,
      value: {
        ...this.state.value,
        [e.idKey]: e
      }
    })
  }

  onSubmit = (e: any) => {
    this.onSave(e)
  }

  componentDidMount(): void {
    if (this.state.contentId) {
      this.getData()
    }
  }

  getData = () => {
    const token = this.props.StoragesSystem?.users?.token || ""
    if (!this.state.isFetching) {
      this.setState({ isFetching: true, isFound: false, isError: false }, () => {
        const config: ApiBody = {
          endpoint: `api/accounts/${token}/get/${this.state.contentId}`,
        }
        new ServerRequest(BASE_URL).get(config, ({ data, isFound, isError, isFetching }) => {
          if (isFound) {
            const value = generateValue(data)
            this.setState({ isFound, isError, isFetching, value })
          } else if (isError) {
            this.setState({ isFound, isError, isFetching })
          }
        })
      })
    }
  }

  onSave = (e: any) => {
    if (this.isReady()) {
      const token = this.props.StoragesSystem?.users?.token || ""
      const config: ApiBody = {
        endpoint: this.state.contentId ? `api/accounts/${token}/update/${this.state.contentId}` : `api/accounts/${token}/create`,
        body: getValue(this.state.value)
      }
      if (!this.state.isFetching) {
        this.setState({ isFetching: true })
        new ServerRequest(BASE_URL).post(config, ({ data, isFound, isError, isFetching, message }) => {
          if (isFound) {
            this.setState({ isFound, isError, isFetching, message })
            this.openModalWindow.current?.show?.()
          } else if (isError) {
            this.setState({ isFound, isError, isFetching, message })
          }
        })
      }
    }

  }

  isReady = (): boolean => {
    const INPUT = GetInput(this.state.contentId ? "edit" : "save")
    const length = Object.keys(this.state.value).length;
    let ready = length >= INPUT.length ? true : false;
    const value = this.state.value;
    for (let i = 0; i < INPUT.length; i++) {
      const key: string = INPUT[i].idKey
      const data = value[key];
      if (!data || data?.isError) {
        ready = false
        value[key] = { ...data, isError: true }
      }
    }
    this.setState({
      value: value
    })
    return ready
  }

  onBack = () => {
    if (this.openModalWindow.current) {
      this.openModalWindow.current?.dismiss()
    }
  }

  onDismiss = () => {
    if (this.props.history.length > 1) {
      this.props.history.goBack();
    } else {
      this.props.history.push('/');
    }
  }

  render() {
    const { props, state } = this;
    return (
      <ProtectorContainer>
        <HeaderFeedsDetailsPages isHome={false} title={state.contentId ? `Mengubah data ${state.value?.["name"]?.value}` : "Menambahkan Ibu Melahirkan"} />
        <Container className={"login_page_wrapperd"}>
          <section className={"content_value content_value_login add_page_wrapper"}>

            {state.contentId && (
              <h5 className="text_header">Mengubah data ibu {state.value?.["name"]?.value} yang sudah melahirkan</h5>
            )}
            {!state.contentId && (
              <h5 className="text_header">Menambahkan ibu yang sudah melahirkan</h5>
            )}
            <h5 className="text_header_second">informasi dibutuhkan agar bisa di track masa nifasnya</h5>
            {(GetInput(state.contentId ? "edit" : "save"))?.map((itemProps, index) => (
              <LazyFoxInputBox key={index} {...itemProps} {...state.value[itemProps.idKey]} onChange={this.onChange} onSubmit={this.onSubmit} />
            ))}
            {state.isError &&
              <div className="error-info">
                <span>{state.message}</span>
              </div>
            }
            <ButtonMain isDark={true} onClick={this.onSave} >
              <span>Simpan Data</span>
            </ButtonMain>
            <LazyFoxModalWindow onDismiss={this.onDismiss} useCloseOnClickBackground={false} className={"modal-window-center"} ref={this.openModalWindow} useHeader={true} headerTitle={state.contentId ? "Edit Info" : "Add Info"}>
              <div className="success-info">
                <div className="text-success">
                  <h2>{state.contentId ? "Pengubahan data berhasil" : "Penambahan data berhasil"}</h2>
                </div>
                <ButtonMain isDark={true} onClick={this.onBack}>
                  <span>Lihat List</span>
                </ButtonMain>
              </div>
            </LazyFoxModalWindow>
          </section>
        </Container>
        <GlobalLoader isShow={state.isFetching} />
      </ProtectorContainer>
    )
  }
}

const mapStateToProps = (state: any) => ({
  StoragesSystem: state.StoragesSystem,
})
const mapDispatchToProps = (dispatch: Dispatch) => ({
  setStorage: ({ data, type }: ParsingCommentsDataBundlePropType) => dispatch(new StoragesSystem().setStorage({ data, type })),
})

export default withRouter(connect(mapStateToProps, mapDispatchToProps)(ScreenAdd));
