import React from 'react';
import ReactDOM from 'react-dom';
import '@assets/sass/main.scss'
import { MainApps, RouterPropType } from 'index-module';
import ScreenHome from '@screens/screen-home';
import screenAdd from '@screens/screen-add';
import screenInfo from '@screens/screen-info';
import screenLogin from '@screens/screen-login';
import screenUsers from '@screens/screen-users';
import screenUsersAdd from '@screens/screen-users-add';

const dataRouteMap: RouterPropType = [
  {
    component: ScreenHome,
    exact: true,
    path: '/'
  },
  {
    component: screenAdd,
    exact: true,
    path: '/add'
  },
  {
    component: screenAdd,
    exact: true,
    path: '/edit/:contentId'
  },

  {
    component: screenUsersAdd,
    exact: true,
    path: '/users/edit/:contentId'
  },
  {
    component: screenUsersAdd,
    exact: true,
    path: '/users/add'
  },
  {
    component: screenUsers,
    exact: true,
    path: '/users'

  },

  {
    component: screenInfo,
    exact: true,
    path: '/info'

  },
  {
    component: screenLogin,
    exact: true,
    path: '/login'
  },
  {
    component: screenInfo,
    exact: true,
    path: '/*'
  }
];

class Apps extends React.Component {
  render() {
    return (
      <MainApps renderFrom={"browser"} dataRouteMap={dataRouteMap} />
    )
  }
}

export default Apps;


