import React from 'react';
import { LazyFoxInputBox, LazyFoxModalWindow, OwnIcon } from 'index-module';
import { ButtonMain } from '@module-button/buttons/index.main.button';
import { uuidv4 } from '@utils/utils-globals';
import FlatList from './index.flat-list';
import "./_records_item.scss"
import { EditIcon, Icons, Trash } from './icons';
import { RecordsItem } from './index.records-item';
import { IUsers, Schedules } from "./type"
import { Redirect, RouteComponentProps } from 'react-router-dom';
import { ApiBody, BASE_URL, ServerRequest } from '@utils/utils-server-request';
import { GlobalLoader } from '@loader/global-loader';
import { getBirtDay } from './utils';

interface FlatListStateType {
  isOpen: any,
  value: any,
  isFound?: any, isError?: any, isFetching?: any, message?: any
}

const FlatListPropDefault: any = {
  // item: 
}



interface IFlatItemList extends RouteComponentProps {
  item: IUsers
  index: number
  StoragesSystem?: any
  doRefresh: () => void
}

export class FlatItemList extends React.Component<IFlatItemList, FlatListStateType> {
  constructor(props: any) {
    super(props);
    this.state = {
      isOpen: false,
      value: { dateJoin: null }
    }
  }
  private openModalWindow = React.createRef<LazyFoxModalWindow>()
  private openModalWindowRecords = React.createRef<LazyFoxModalWindow>()
  private openModalWindowCreate = React.createRef<LazyFoxModalWindow>()
  public static defaultProps = FlatListPropDefault;


  onEdit = () => {
    this.props.history.push({
      pathname: `/edit/${this.props.item?.id}`
    })
  }



  onBack = () => {
    if (this.openModalWindow.current) {
      this.openModalWindow.current?.dismiss()
    }
  }

  onOpenPupupDelete = () => {
    if (this.openModalWindow.current) {
      this.openModalWindow.current?.show()
    }
  }

  onOpenPupupRecords = () => {
    if (this.openModalWindowRecords.current) {
      this.openModalWindowRecords.current?.show()
    }
  }

  onOpenPupupCreate = () => {
    if (this.openModalWindowCreate.current) {
      this.openModalWindowCreate.current?.show()
    }
  }


  onDismiss = () => {

  }


  onChange = (e: any) => {
    this.setState({
      isError: false,
      value: {
        ...this.state.value,
        [e.idKey]: e
      }
    })
  }


  onCreateRecords = (user: IUsers) => (e: any) => {
    const token = this.props.StoragesSystem?.users?.token || ""
    if (!this.state.value.dateJoin?.isError) {
      const config: ApiBody = {
        endpoint: `api/records/${token}/create`,
        body: {
          dateJoin: this.state.value.dateJoin.value,
          userId: user.id
        }
      }
      if (!this.state.isFetching) {
        this.setState({ isFetching: true })
        new ServerRequest(BASE_URL).post(config, ({ data, isFound, isError, isFetching, message }) => {
          if (isFound) {
            this.setState({ isFound, isError, isFetching, message }, () => {
              this.props.doRefresh()
              this.openModalWindowCreate.current?.dismiss?.()
            })
          } else if (isError) {
            this.setState({ isFound, isError, isFetching, message })
          }
        })
      }
    }

  }


  onDeleteUserById = (user: IUsers) => (e: any) => {
    const token = this.props.StoragesSystem?.users?.token || ""
    if (!this.state.value.dateJoin?.isError) {
      const config: ApiBody = {
        endpoint: `api/accounts/${token}/delete/${user.id}`,
      }
      if (!this.state.isFetching) {
        this.setState({ isFetching: true })
        new ServerRequest(BASE_URL).delete(config, ({ data, isFound, isError, isFetching, message }) => {
          if (isFound) {
            this.setState({ isFound, isError, isFetching, message }, () => {
              this.props.doRefresh()
              this.openModalWindow.current?.dismiss?.()
            })
          } else if (isError) {
            this.setState({ isFound, isError, isFetching, message })
          }
        })
      }
    }
  }

  getActiveSchedule = (schedule: Schedules[] = []) => {
    for (let i = 0; i < schedule.length; i++) {
      const item = schedule[i];
      if (!item.isLate) {
        return getBirtDay(item.date).full
      }
    }
    return 'tidak ada jadwal'
  }

  render() {
    const { props, state } = this;

    return (
      <div className={"item-list-flat-list"} >
        <div className={"item-list-flat-list-inside"}>
          <div className={`medicine-title ${props.item.Records?.[0]?.isExpired && "expired_key"}`} >
            <span className='numbering'>{(props.index + 1)}</span>
            <span>{props.item.name}</span>
          </div>
          <div className={"medicine-content"}>
            <Icons />
            <div className={'main-contents'}>

              <div className={"info"} >
                <>
                  <span className={"alias"}>Nama:  <span>Ibu {props.item?.name}</span> </span>
                  <span className={"price"}>Tanggal Lahiran: <span>{getBirtDay(props.item.Records[0]?.dateJoin).full}</span></span>
                  <span className={"stok"}>Jadwal: <span className={""}>{this.getActiveSchedule(props.item.Records?.[0]?.schedules)}</span></span>
                </>

              </div>
              <div className={"editor-container"}>
                <>
                  <span className={"button-chart"}>
                    <ButtonMain isDark={true} onClick={this.onOpenPupupRecords}>
                      <span>LIHAT</span>
                    </ButtonMain>
                  </span>
                  <span onClick={this.onEdit}>
                    <EditIcon />
                  </span>
                </>
                <span onClick={this.onOpenPupupDelete}>
                  <Trash />
                </span>
              </div>
            </div>
          </div>
        </div>

        <LazyFoxModalWindow onDismiss={this.onDismiss} className={"modal-window-center"} ref={this.openModalWindow} useHeader={true} headerTitle={"PERINGATAN !!!"}>
          <div className="success-info">
            <div className="text-success">
              <h2>Apakah anda yakin ingin menghapus {props.item.name} ?</h2>
            </div>
            <div className='d_flex justify_content_between'>
              <ButtonMain className='dark-key' isDark={true} onClick={this.onBack}>
                <span>Batalkan</span>
              </ButtonMain>
              <ButtonMain isDark={true} onClick={this.onDeleteUserById(props.item)}>
                <span>Hapus</span>
              </ButtonMain>
            </div>
          </div>
        </LazyFoxModalWindow>

        <LazyFoxModalWindow icon={() => <OwnIcon.IconAddNewEpisode style={{ fill: "#fff" }} onClick={this.onOpenPupupCreate} />} onDismiss={this.onDismiss} className={"modal-window-left"} ref={this.openModalWindowRecords} useHeader={true} headerTitle={"Daftar Persalinan"}>
          <div className="success-info ">
            <FlatList >
              {() => (
                <>
                  <div className='records_item' >
                    {props.item.Records.map((recordItem, i) => (
                      <RecordsItem {...props} user={props.item} record={recordItem} index={i} key={i}></RecordsItem>
                    ))}
                  </div>
                </>
              )}
            </FlatList>
          </div>
        </LazyFoxModalWindow>

        <LazyFoxModalWindow onDismiss={this.onDismiss} className={"modal-window-left"} ref={this.openModalWindowCreate} useHeader={true} headerTitle={"Waktu Persalinan"}>
          <div className="success-info ">
            <div className="text-success">
              <h2>Anda akan menambahkan Tanggal bersalin untuk</h2>

              <h2> ibu {props.item.name}</h2>
              <div>
                <LazyFoxInputBox idKey={"dateJoin"} onChange={this.onChange} title='Tanggal Bersalin' type='date' />
              </div>
            </div>
            <ButtonMain isDark={true} onClick={this.onCreateRecords(props.item)}>
              <span>Simpan</span>
            </ButtonMain>

            <GlobalLoader isShow={state.isFetching} />
          </div>
        </LazyFoxModalWindow>

      </div>
    )
  }
}
