import { ParsingCommentsDataBundlePropType } from "@interfaces/data-type/type.pages-comments";
import { Container, DataArray, Dispatch, HeaderFeedsDetailsPages, LazyFoxInputBox, LazyFoxModalWindow, LazyFoxModalWindowPropType, StoragesSystem, } from "index-module";
import React, { Component } from "react";
import { connect } from "react-redux";
import { withRouter } from "react-router-dom";
import { ScreenHomePropType, ScreenHomePropTypeSmall } from "./screen-home";
import { ButtonMain } from "@module-button/buttons/index.main.button";
import { uuidv4 } from "@utils/utils-globals";
import { ApiBody, BASE_URL, ServerRequest } from "@utils/utils-server-request";
import { GlobalLoader } from "@loader/global-loader";
import { Route, Redirect } from 'react-router-dom';
import ProtectorContainer from "@module-layout/protector-container";

export interface ScreenAddStateType {
  catalog: any,
  key: any,
  value: any,
  contentId?: any,
  indexId?: any
  isFound?: any, isError?: any, isFetching?: any,
  message?: any
}

const FormLoginLogicPropDefault: ScreenHomePropTypeSmall = {
  StoragesSystem: {
    catalog: []
  },
}

const ENUM_INPUT = [
  { title: "Email", type: "email", idKey: 'email' },
  { title: "Password", type: "password", idKey: 'password', },
];

const generateValue = (obj: any = {}) => {
  let newObject = {};
  for (const key in obj) {
    newObject = {
      ...newObject,
      [key]: {
        isError: false,
        value: obj[key]
      }
    };
  }
  return newObject
}

const getValue = (obj: any = {}) => {
  let newObject = {};
  for (const key in obj) {
    newObject = {
      ...newObject,
      [key]: obj[key].value
    };
  }
  return newObject
}

class ScreenLogin extends Component<ScreenHomePropType, ScreenAddStateType> {
  static defaultProps = FormLoginLogicPropDefault;
  constructor(props: any) {
    super(props)
    this.state = {
      catalog: props.StoragesSystem?.catalog || [],
      key: 'title',
      isError: false,
      contentId: props.match?.params?.contentId,
      indexId: props.match?.params?.indexId,
      value: {
        email: {},
        password: {}
      }
    }
  }
  private openModalWindow = React.createRef<LazyFoxModalWindow>()

  onChange = (e: any) => {
    this.setState({
      isError: false,
      value: {
        ...this.state.value,
        [e.idKey]: e
      }
    })
  }

  onSubmit = (e: any) => {
    this.onSave(e)
  }

  onSave = (e: any) => {
    if (this.isReady()) {
      const config: ApiBody = {
        endpoint: 'api/accounts/login',
        body: getValue(this.state.value)
      }
      if (!this.state.isFetching) {
        this.setState({ isFetching: true })
        new ServerRequest(BASE_URL).post(config, ({ data, isFound, isError, isFetching, message }) => {
          if (isFound) {
            this.setState({ isFound, isError, isFetching, message })
            this.props.setStorage?.({ data: data, type: 'users' });
          } else if (isError) {
            this.setState({ isFound, isError, isFetching, message })
          }
        })
      }
    }

  }

  isReady = (): boolean => {
    const length = Object.keys(this.state.value).length;
    let ready = length > 1 ? true : false;
    for (const key in this.state.value) {
      const data = this.state.value[key];
      if (data.value === undefined || data.isError) {
        ready = false
        this.setState({
          value: { ...this.state.value, [key]: { ...data, isError: true } }
        })
        break;
      }

    }
    return ready
  }

  onBack = () => {
    if (this.openModalWindow.current) {
      this.openModalWindow.current?.dismiss()
    }
  }

  onDismiss = () => {
    if (this.props.history.length > 1) {
      this.props.history.goBack();
    } else {
      this.props.history.push('/');
    }
  }

  render() {
    const { props, state } = this;
    return (
      <ProtectorContainer isLogin={true}>
        <HeaderFeedsDetailsPages isHome={true} title={"Login Page"} />
        <Container className={"login_page_wrapper"}>
          <section className={"content_value content_value_login login_page_wrapper"}>
            <h5 className="text_header">Selamat datang</h5>
            <h5 className="text_header_second">anda harus masuk terlebih dahulu</h5>
            {ENUM_INPUT?.map((itemProps, index) => (
              <LazyFoxInputBox key={index} {...itemProps} {...state.value[itemProps.idKey]} onChange={this.onChange} onSubmit={this.onSubmit} />
            ))}
            {state.isError &&
              <div className="error-info">
                <span>{state.message}</span>
              </div>
            }
            <ButtonMain isDark={true} onClick={this.onSave} >
              <span>Masuk</span>
            </ButtonMain>
            <LazyFoxModalWindow onDismiss={this.onDismiss} useCloseOnClickBackground={false} className={"modal-window-center"} ref={this.openModalWindow} useHeader={true} headerTitle={state.indexId ? "Edit Info" : "Add Info"}>
              <div className="success-info">
                <div className="text-success">
                  <h2>{state.indexId ? "Pengubahan data berhasil" : "Penambahan data berhasil"}</h2>
                </div>
                <ButtonMain isDark={true} onClick={this.onBack}>
                  <span>Lihat List</span>
                </ButtonMain>
              </div>
            </LazyFoxModalWindow>
          </section>
        </Container>
        <GlobalLoader isShow={state.isFetching} />
      </ProtectorContainer>
    )
  }
}

const mapStateToProps = (state: any) => ({
  StoragesSystem: state.StoragesSystem,
})
const mapDispatchToProps = (dispatch: Dispatch) => ({
  setStorage: ({ data, type }: ParsingCommentsDataBundlePropType) => dispatch(new StoragesSystem().setStorage({ data, type })),
})

export default withRouter(connect(mapStateToProps, mapDispatchToProps)(ScreenLogin));
