export const getBirtDay = (str?: string): { day: string, date: string, full: string } => {
  if (!str) return ({
    date: "belum ada",
    day: "belum ada",
    full: "belum ada"
  })
  const day = ["Senin", "Selasa", "Rabu", "Kamis", "Jumat", "Sabtu", "Minggu"]
  const theDay = new Date(str || "");
  const date = theDay.getDate() < 10 ? `0${theDay.getDate()}` : theDay.getDate()
  return {
    date: `${theDay.getFullYear()}-${theDay.getMonth() + 1}-${date}`,
    day: day[theDay.getDay()],
    full: `${day[theDay.getDay()]} ${theDay.getDate()}/${theDay.getMonth() + 1}/${theDay.getFullYear()}`,
  }
}