import React, { Component } from "react";
import { Container } from "../container";
import { OnpediaLogo } from '../onpedia-logo';
import PartGlobalRightHeaderButton from "./element-part/part.global.right-header-button";
import { SearchBox } from '@module-inputs/search-box/index.search-box-input';
import { FooterPropType, FooterPropDefault } from '@interfaces/layout-type/type-layouts';
import { ButtonActionsGoBack, ButtonIconsSecondary, OwnIcon } from "index-module";

class HeaderGlobalPages extends Component<any> {
  static defaultProps = FooterPropDefault;
  render() {
    const { props } = this;
    return (
      <nav className="head_navigation d_flex">
        <Container className={"d_flex"}>
          <div className="d_flex justify_content_between align_items_center header_content">
            <div className={"left_button_container d_flex justify_content_start align_items_center"}>
              {props.isHome &&
                <OnpediaLogo isText={false} />
              }
              {!props.isHome &&
                <ButtonActionsGoBack render={(childProps) => (
                  <ButtonIconsSecondary className={"notification_btn"}>
                    <OwnIcon.IconBack className={"icons"} />
                  </ButtonIconsSecondary>
                )} />
              }
              <SearchBox {...props} />
            </div>
            {props.useBack &&
              <div style={{ width: '40px' }}>

              </div>
            }
            {!props.useBack &&
              <PartGlobalRightHeaderButton {...props} activePage={props.activePage} />
            }
          </div>
        </Container>
      </nav>
    )
  }
}

export { HeaderGlobalPages };
