import { IUsers, Schedules } from "./type";
import { getBirtDay } from "./utils";

const getMessage = (date: string, name: string, adminName: string) => (`

Assalamualaikum 
Selamat pagi

Saya *${adminName}* dari *puskesmas Bojong 1*

Mengingatkan ibu *${name}*

Saatnya kontrol nifas Ke ruang persalinan puskesmas Bojong 1 paling lambat pada tanggal *${getBirtDay(date).full}*

Untuk melakukan pemeriksaan pasca bersalin 

Terimakasih

`)

const getMessageLate = (date: string, name: string, adminName: string, lateDate: string) => (`

  Assalamualaikum 
  Selamat pagi
  
  Saya *${adminName}* dari *puskesmas Bojong 1*
  
  Mengingatkan ibu *${name}* telat kontrol nifas
  
  diharapkan selanjutnya dapat melakukan kontrol nifas Ke ruang persalinan puskesmas Bojong 1 paling lambat pada tanggal *${getBirtDay(lateDate).full}*
  
  Untuk melakukan pemeriksaan pasca bersalin 
  
  Terimakasih
  
  `)

export const WhatsappMessage = (record: Schedules[], user: IUsers, admin: IUsers, index: number) => () => {
  const phone = parseInt(user.phone)
  let lateDate = record[index].date;
  for (let i = 0; i < record.length; i++) {
    const item = record[i];
    if (!item.isLate) {
      lateDate = item.date;
      break
    }
  }
  const message = record[index].isLate ? getMessageLate(record[index].date, user.name, admin.name, lateDate) : getMessage(record[index].date, user.name, admin.name);
  const link = `https://wa.me/+62${phone}?text=${encodeURIComponent(message)}`;
  window.open(link, '_blank');
}