import { OwnIcon } from 'index-module';
import React from 'react';


interface IGlobalLoader {
  isShow: boolean
}

export const GlobalLoader: React.FC<IGlobalLoader> = (props) => {
  if (!props.isShow) {
    return null
  }
  return (
    <div className='global_loader'>
      <OwnIcon.IconLoader />
    </div>
  )
}