import React from 'react';
import { Link } from 'react-router-dom';

export interface ButtonIconsMainPropType {
  onClick: (event: object) => void,
  placeholder: string,
  children: any,
  isActive: boolean,
  isDark: boolean,
  isUseAlert: boolean,
  alertValue: number,
  isDesktop: boolean
  to: string,
  className: string,
}
export const ButtonIconsMainPropDefault: ButtonIconsMainPropType = {
  placeholder: 'button',
  onClick: () => void (0),
  children: null,
  isActive: false,
  isDark: false,
  isUseAlert: false,
  alertValue: 0,
  isDesktop: false,
  className: '',
  to: '/',
}

export class ButtonIconsMain extends React.Component<ButtonIconsMainPropType> {
  constructor(props: any) {
    super(props);
  }
  public static defaultProps = ButtonIconsMainPropDefault;

  onClick = (event: any) => {
    this.props.onClick({ event });
  }

  calculateAlertValue = (value: number): string | number => {
    if (value > 10) {
      return '10+';
    } else {
      return value
    }
  }
  render() {
    const { props } = this;
    return (
      <Link className={props.isDesktop ? 'desktop-items' : ''} to={props.to}>
        <div className={`${props.isDark ? 'button_icons_dark' : 'button_icons'} ${props.className}`} onClick={this.onClick}>
          <div className={props.isActive ? "button_icons_box active" : "button_icons_box"} >
            <div className={"button_icons_bandle"}>
              {props.children}
              {props.isUseAlert && props.alertValue > 0 &&
                <div className="alert_info">
                  <label className="alert_info_text">{this.calculateAlertValue(props.alertValue)}</label>
                </div>
              }
            </div>
            {props.placeholder !== '' &&
              <label className={"text"} >{props.placeholder}</label>
            }
          </div>
        </div>
      </Link>
    )
  }
}
