import { ParsingCommentsDataBundlePropType } from "@interfaces/data-type/type.pages-comments";
import { Dispatch, StoragesSystem, } from "index-module";
import React, { Component, ComponentType } from "react";
import { connect } from "react-redux";
import { withRouter } from "react-router-dom";
import { Redirect, RouteComponentProps } from 'react-router-dom';

export interface IProtectorContainer extends RouteComponentProps {
  isLogin?: boolean
  children?: any,
  StoragesSystem?: any
}
const ProtectorContainerPropDefault: any = {
  isLogin: false,
  StoragesSystem: {
    catalog: []
  },
}

class ProtectorContainer extends Component<IProtectorContainer, any> {
  static defaultProps = ProtectorContainerPropDefault;
  constructor(props: IProtectorContainer) {
    super(props)
  }
  render() {
    const { props } = this;
    if (!props.StoragesSystem.users?.token && !props.isLogin) {
      return (
        <Redirect to={"/login"} />
      )
    } else if (props.isLogin && props.StoragesSystem.users?.token) {
      return (
        <Redirect to={"/"} />
      )
    }
    return props.children
  }
}

const mapStateToProps = (state: any) => ({
  StoragesSystem: state.StoragesSystem,
})
const mapDispatchToProps = (dispatch: Dispatch) => ({
  setStorage: ({ data, type }: ParsingCommentsDataBundlePropType) => dispatch(new StoragesSystem().setStorage({ data, type })),
})

export default withRouter<IProtectorContainer, ComponentType<IProtectorContainer>>(connect(mapStateToProps, mapDispatchToProps)(ProtectorContainer));
