import React, { Component } from "react";
import { Container } from "../container";
import { ButtonIconsMain } from '@module-button/button-icons/index.main.button';
import { OnpediaLogo } from '../onpedia-logo';
import { OwnIcon } from '@icons/module-icons';
import { FooterPropType, FooterPropDefault } from '@interfaces/layout-type/type-layouts';

interface IFooterMobileGlobal {
  activePage?: string
  useFooter?: boolean
  StoragesSystem?: any
}
class FooterMobileGlobal extends Component<IFooterMobileGlobal> {
  static defaultProps = FooterPropDefault;
  render() {
    const { props } = this;
    if (props.useFooter) {
      const chart: any = props.StoragesSystem?.chart || [];
      return (
        <footer className="footer_navigation">
          <Container className={"d_flex"}>
            <div className="d_flex justify_content_between align_items_center header_content">
              <div className={"right_button_container d_flex justify_content_between align_items_center"}>
                <ButtonIconsMain to={"/"} placeholder={"Laporan"} isActive={props.activePage === 'home' ? true : false} isDark={true}>
                  <OwnIcon.IconFeeds className={"icons"} />
                </ButtonIconsMain>
                <ButtonIconsMain to={"/add"} placeholder={"Tambah"} isActive={props.activePage === 'tambah' ? true : false} isDark={true}>
                  <OwnIcon.IconAddNewEpisode className={"icons"} />
                </ButtonIconsMain>
                <ButtonIconsMain isUseAlert={chart.length > 0} alertValue={chart.length} to={"/users"} placeholder={"users"} isActive={props.activePage === 'users' ? true : false} isDark={true}>
                  <OwnIcon.IconUser className={"icons"} />
                </ButtonIconsMain>
                <ButtonIconsMain to={"/info"} placeholder={"info"} isActive={props.activePage === 'info' ? true : false} isDark={true}>
                  <OwnIcon.IconAlert className={"icons"} />
                </ButtonIconsMain>

              </div>
            </div>
          </Container>
        </footer>
      )
    } else {
      return null
    }
  }
}
export { FooterMobileGlobal };