import apisauce from 'apisauce';

export const BASE_URL = `${window.location.origin}`;
// export const BASE_URL = `http://${window.location.hostname}:3000`;
const DEFAULT_TIMEOUT = 60000;

type Callback = (response: Response) => void;

interface Headers {
  "Content-Type"?: string,
  "Accept"?: string,
  Authorization?: string,
}

interface ThrowCallback {
  callback: Callback,
  data?: any,
  isFetching?: boolean,
  isFound?: boolean,
  isError?: boolean,
  message?: any
}

interface Response {
  data: any,
  isFetching: boolean,
  isFound: boolean,
  isError: boolean,
  message?: any
}

interface CallbackSend {
  data: any,
  state: Response,
}

export interface ApiBody {
  endpoint: string,
  body?: any,
  token?: string,
  headersType?: Headers,
  responseType?: string,

}
interface CreateApiRequest {
  token?: string,
  responseType?: string,
  headersType?: Headers,
  baseUrl: string,
}

const throwCallback = ({ callback, data = null, isFound = false, isFetching = false, isError = false, ...resProps }: ThrowCallback) => {
  callback(
    { ...resProps, data, isFetching, isFound, isError },
  );
}

const createApiRequest = ({ token, responseType, headersType, baseUrl }: CreateApiRequest) => {
  const headers: Headers = {
    // 'Content-Type': 'application/json',
    // 'Accept': 'application/json',
    ...headersType,
  }
  if (token) { headers.Authorization = `BEARER ${token}` }
  const apiConfig: any = {
    baseURL: baseUrl,
    timeout: DEFAULT_TIMEOUT,
    headers: headers,
    responseType: 'application/json'
  }
  if (responseType) { apiConfig.responseType = responseType; }
  return apisauce.create(apiConfig);
}

class ServerRequest {
  constructor(baseUrl: string) {
    if (baseUrl) {
      this.baseUrl = baseUrl;
    }
  }
  private baseUrl: string = BASE_URL;
  private getData = async ({ endpoint, body, token, responseType, headersType }: ApiBody, method: string, callback: Callback) => {
    const api: any = createApiRequest({ token, responseType, headersType, baseUrl: this.baseUrl });

    throwCallback({ callback, isFetching: true });

    try {
      const response = await api[method](endpoint, body);
      const { data, status } = response;
      if (status === 200 && typeof data === 'object') {
        const newData = data.data || data;
        throwCallback({ ...data, callback, data: newData, isFound: true })
      } else {
        throwCallback({ ...data, callback, data, isError: true })
      }
    } catch (data) {
      throwCallback({ callback, data, isError: true, })
    }
  }

  get = async (apiBody: ApiBody, callback: Callback) => {
    this.getData(apiBody, 'get', callback);
  }

  post = async (apiBody: ApiBody, callback: Callback) => {
    this.getData(apiBody, 'post', callback);
  }

  delete = async (apiBody: ApiBody, callback: Callback) => {
    this.getData(apiBody, 'delete', callback);
  }

}
export { ServerRequest }
