class PartGlobalRightHeaderButton extends Component<FooterPropType> {
  static defaultProps = FooterPropDefault;
  render() {
    const { props } = this;
    return (
      <div className={"right_button_container d_flex justify_content_between align_items_center"}>

        <ButtonIconsMain to={props.backBtnPath || "/add"} isDesktop={true} placeholder={"Tambah"} isActive={props.activePage === 'add' ? true : false}>
          <OwnIcon.IconAddNewEpisode className={"icons"} />
        </ButtonIconsMain>
      </div>
    )
  }
}
export default PartGlobalRightHeaderButton;
